<template>
  <div class="calssDialogBox">
    <el-dialog
      :title="dialogTitle"
      :visible="dialogFormVisible"
      width="40%"
      v-if="dialogFormVisible"
      @close="classDialogClose"
    >
      <el-form :model="classForm" :rules="rules" ref="classForm">
        <el-form-item label="院系" :label-width="formLabelWidth" prop="departmentKey">
          <el-select v-model="classForm.departmentKey" placeholder="请选择你的学院" @change="changeDepart">
            <el-option
              v-for="(item,index) in departmentList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="专业" :label-width="formLabelWidth" prop="professionKey">
          <el-select
            v-model="classForm.professionKey"
            placeholder="请选择你的专业"
            @change="changeProfssion"
          >
            <el-option
              v-for="(item,index) in professionList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="班级" :label-width="formLabelWidth" prop="classNo">
          <el-select v-model="classForm.classNo" placeholder="请选择你的班级">
            <el-option
              v-for="(item,index) in classList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="班级人数" :label-width="formLabelWidth" prop="classNum">
          <el-input v-model="classForm.classNum" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label="就业人数" :label-width="formLabelWidth" prop="employNum">
          <el-input v-model="classForm.employNum" autocomplete="off" disabled value=0></el-input>
        </el-form-item>-->
        <el-form-item label="班长" :label-width="formLabelWidth" prop="monitor">
          <el-input v-model="classForm.monitor" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班长联系方式" :label-width="formLabelWidth" prop="monitorTel">
          <el-input v-model="classForm.monitorTel" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">取 消</el-button>
        <el-button type="primary" @click="submitForm('classForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { isPhone } from "./../../../../../utils/utils";
import { addNewClass, updateClass,getProfessionAttach } from "../../../../../utils/employment.url";
export default {
  props: ["dialogFormVisible", "classForm", "dialogTitle"],
  data() {
    return {
      departmentList: [], //学院列表
      professionList: [], //专业列表
      classList: [], //班级列表
      attachList: [], //院系信息列表
      formLabelWidth: "120px",
      rules: {
        classNo: [
          { required: true, message: "请选择班级", trigger: "blur" },
        ],
        departmentKey: [
          { required: true, message: "请选择院系", trigger: "blur" }
        ],
        professionKey: [
          { required: true, message: "请选择专业", trigger: "blur" }
        ],
        classNum: [
          { required: true, message: "请输入班级人数", trigger: "blur" }
        ],
        monitor: [
          { required: true, message: "请输入班长姓名", trigger: "blur" }
        ],
        monitorTel: [{ required: true, validator: isPhone, trigger: "blur" }]
      }
    };
  },
  methods: {
    //关闭
    classDialogClose() {
      this.$refs["classForm"].resetFields();
      this.$emit("changeDialogVisible", false);
    },
    //,取消
    cancelEdit() {
      // this.dialogFormVisible = false;
      this.$emit("changeDialogVisible", false);
    },
    //submitForm
    submitForm(formName) {
      console.log(this.classForm)
      if (this.dialogTitle == "修改班级信息") {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.updateClass();
          } else {
            return false;
          }
        });
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.addNewClass();
          } else {
            return false;
          }
        });
      }
    },
    //添加新班级
    addNewClass() {
      this.classForm.employNum = 0;
      addNewClass(this.classForm, "post")
        .then(res => {
          if (res.status == 200) {
            this.$emit("changeDialogVisible", false);
            this.$message({
              message: res.message,
              type: "success"
            });
          } else if (res.status == 401) {
            this.$message({
              message: res.message,
              type: "warning"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //修改新班级
    updateClass() {
      this.$emit("changeDialogVisible", false);
      updateClass(this.classForm, "put").then(res => {
        if (res.status == 200) {
          this.$emit("updateClass");
          this.$message({
            message: res.message,
            type: "success"
          });
        }
      });
    },
    beforeClose() {
      this.dialogFormVisible = false;
      this.$emit("changeDialogVisible", this.dialogFormVisible);
    },
    getProfessionAttach(session, departmentKey, professionKey, classKey) {
      var params = {
        session: session ? session : "",
        departmentKey: departmentKey ? departmentKey : "",
        professionKey: professionKey ? professionKey : "",
        classKey: classKey ? classKey : ""
      };
      getProfessionAttach(params, "get").then(res => {
        if (res.status == 200) {
          this.attachList = res.data.attachList;
          // this.departmentList = this.groupBy(
          //   this.attachList,
          //   "deparymentKey",
          //   "departmentName"
          // );
          this.departmentList = this.groupBy(this.attachList, function(item) {
            return [item.departmentKey, item.departmentName]; //按照时间年月分组
          });
        }
      });
    },
    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function(o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function(group) {
        var _group =JSON.parse(group)
        return {
          key:_group[0],
          name:_group[1],
          child:groups[group]
        };
      });
    },
    changeDepart(key){
      this.classForm.professionKey='',
      this.departmentList.map((item,index) =>{
        if(item.key == key){
          this.professionList = this.groupBy(item.child, function(item) {
            return [item.professionKey, item.professionName]; //按照时间年月分组
          });
          // console.log(this.professionList)
        }
      })
    },
    changeProfssion(key){
      // this.classForm.classNo='',
      this.professionList.map((item,index) =>{
        if(item.key == key){
          this.classList = this.groupBy(item.child, function(item) {
            return [item.classKey, item.className]; //按照时间年月分组
          });
        }
      })
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    let session = this.userInfo.session ? this.userInfo.session : "";
    let departmentKey ="";
    let professionKey ="";
    let classKey ="";
    this.getProfessionAttach(session, departmentKey, professionKey, classKey);
  }
  // watch:{
  //   dialogFormVisible(value){
  //     this.$emit('changeDialogVisible',value)
  //   }
  // }
};
</script>
<style lang="scss" scoped>
  .el-dialog {
    .el-form {
      .el-form-item {
        .el-input {
          width: 300px;
        }
        .el-select {
          width: 300px;
        }
      }
    }
  }
</style>