<template>
  <div class="myInfoBox">
    <div class="basisInfo">
      <div class="title">
        <span>我的基础信息</span>
      </div>
      <div class="basisList">
        <p>
          <span>教工号:</span>
          <span>{{counselorInfo.counselorNo}}</span>
        </p>
        <p>
          <span>姓名:</span>
          <span>{{counselorInfo.counselorName}}</span>
        </p>
        <p>
          <span>联系方式:</span>
          <span>{{counselorInfo.counselorTel}}</span>
        </p>
      </div>
    </div>
    <div class="leadClassBox">
      <div class="title">
        <span>我的班级</span>
      </div>
      <div class="classListBox">
        <div class="classList" v-for="(item, index) in counselorInfo.leadClass" :key="index">
          <span>{{item}}</span>
        </div>
        <div class="classList addClass" @click="addNewClass">
          <span>添加班级</span>
        </div>
      </div>
    </div>
    <div class="addClassDialog">
      <calssDialog
        :dialogFormVisible="dialogFormVisible"
        :dialogTitle="dialogTitle"
        :classForm="classForm"
        @changeDialogVisible="changeDialogVisible"
      ></calssDialog>
    </div>
  </div>
</template>
<script>
import {
  interCounselorInfo,
  addCounselorInfo
} from "../../../../../utils/employment.url";
import calssDialog from "./calssDialog.vue";
export default {
  components: { calssDialog },
  data() {
    return {
      userInfo: {},
      counselorInfo: {},
      classForm: {},
      dialogTitle: "添加新班级",
      dialogFormVisible: false
    };
  },
  methods: {
    //获取辅导员基本信息
    getCounselorInfo() {
      interCounselorInfo(
        {
          counselorNo: this.userInfo.userId
        },
        "get"
      )
        .then(res => {
          if (res.status == 200) {
            console.log(res.data.length);
            this.counselorInfo = res.data;
          }
          if (res.status == 401) {
            this.$prompt("请输入您的联系方式", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消"
            })
              .then(({ value }) => {
                this.counselorInfo.counselorTel = value
                addCounselorInfo(this.counselorInfo, "post").then(res => {
                  if (res.status == 200) {
                    this.$message({
                      type: "success",
                      message: ""
                    });
                    this.getCounselorInfo();
                  }
                });
              })
              .catch(() => {
                this.$message({
                  type: "info",
                  message: "取消输入"
                });
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //添加新班级
    addNewClass() {
      this.classForm.counselorNo = this.userInfo.userId;
      this.dialogFormVisible = true;
    },
    //修改dialogFormVisible
    changeDialogVisible(val) {
      this.dialogFormVisible = val;
      this.getCounselorInfo();
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.counselorInfo.counselorNo = this.userInfo.userId;
    this.counselorInfo.counselorName = this.userInfo.userName;
    this.getCounselorInfo();
  }
};
</script>
<style lang="scss" scoped>
.myInfoBox {
  width: 1000px;
  margin: 20px auto;
  .basisInfo .title,
  .leadClassBox .title {
    font-size: 16px;
    font-weight: 600;
  }
  .basisInfo .basisList {
    margin: 20px;
    :nth-child(n) {
      display: inline-block;
      margin-left: 10px;
    }
    p {
      font-family: "华文新魏";
      :nth-of-type(2n + 1) {
        font-size: 16px;
        font-weight: 600;
      }
      :nth-of-type(2n) {
        color: #999595;
      }
    }
  }

  .leadClassBox {
    .classListBox {
      .classList {
        display: inline-block;
        width: 180px;
        height: 50px;
        text-align: center;
        margin: 10px;
        border-radius: 5px;
        background: #8bbaf8;
        cursor: pointer;

        span {
          font-size: 20px;
          line-height: 50px;
          color: #999595;
        }
      }

      .addClass {
        background: #1981f8;
        span {
          color: #ffffff;
        }
      }
      :hover {
        background: #ffffff;
        span {
          color: #1981f8;
        }
      }
    }
  }
}
</style>